/* Skills */
.Skills {
  background-color: white;
  border: 10px solid gray;
  height: auto;
  width: 100%;
  box-shadow: 0px 3px 3px -2px rgb(0 0 0 / 15%),
    0px 3px 4px 0px rgb(0 0 0 / 12%), 0px 1px 8px 0px rgb(0 0 0 / 30%);
}
.skills-img {
  justify-content: center;
  flex-basis: 30%;
  display: flex;
  flex-wrap: wrap;
}
.s1:not(:first-child) {
  border-top: 3px solid black;
}
/* Skills 아이콘 마우스오버효과 */

.skills-img .skills-ico {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 200px;
  width: 200px;
  position: relative;
  overflow: hidden;
  margin-bottom: 20px;
}

.skills-img > .skills-ico::after {
  border-radius: 5px;
  content: "";
  background-color: rgba(0, 0, 0, 0);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: background-color 0.5s;
}

.skills-img > .skills-ico:before {
  font-weight: bold;
  position: absolute;
  top: 50%;
  left: 50%;
  color: white;
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
  padding: 10px;
  border-radius: 10px;
  z-index: 1;
  opacity: 0;
  transition: opacity 0.5s;
}
.s-html::before {
  content: "HTML";
}
.s-css::before {
  content: "CSS";
}
.s-js::before {
  content: "JavaScript";
}
.s-react::before {
  content: "React";
}

.s-ts::before {
  content: "TypeScript";
}
.s-py::before {
  content: "Python";
}
.s-java::before {
  content: "Java";
}
.s-tailwind::before {
  content: "Tailwindcss";
}
.s-jquery::before {
  content: "Jquery";
}
.s-bootstrap::before {
  content: "Bootstrap";
}
.s-github::before {
  content: "Github";
}
.s-git::before {
  content: "Git";
}
.s-nextjs::before {
  content: "Next.js";
}
.skills-ico:hover::after {
  background-color: rgba(0, 0, 0, 0.5);
}

.skills-img > .skills-ico:hover::before {
  opacity: 1;
}

.skills-img > .skills-ico > img {
  transition: -webkit-transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
}
.skills-img > .skills-ico:hover > img {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}
